import React, { useState } from "react";
import classNames from "classnames";
import { SectionProps } from "../../utils/SectionProps";
import ButtonGroup from "../elements/ButtonGroup";
import Button from "../elements/Button";
import Image from "../elements/Image";
import Modal from "../elements/Modal";

const bookmark = (e) => {
  var bookmarkURL = window.location.href;
  var bookmarkTitle = document.title;
  var triggerDefault = false;

  if (window.sidebar && window.sidebar.addPanel) {
    // Firefox version < 23
    window.sidebar.addPanel(bookmarkTitle, bookmarkURL, "");
  } else if (
    (window.sidebar &&
      navigator.userAgent.toLowerCase().indexOf("firefox") > -1) ||
    (window.opera && window.print)
  ) {
    // Firefox version >= 23 and Opera Hotlist
    triggerDefault = true;
  } else if (window.external && "AddFavorite" in window.external) {
    // IE Favorite
    window.external.AddFavorite(bookmarkURL, bookmarkTitle);
  } else {
    // WebKit - Safari/Chrome
    alert(
      (navigator.userAgent.toLowerCase().indexOf("mac") != -1
        ? "Cmd"
        : "Ctrl") + "+D 키를 눌러 즐겨찾기에 등록하실 수 있습니다."
    );
  }

  return triggerDefault;
};
const propTypes = {
  ...SectionProps.types,
};

const defaultProps = {
  ...SectionProps.defaults,
};

const Hero = ({
  className,
  topOuterDivider,
  bottomOuterDivider,
  topDivider,
  bottomDivider,
  hasBgColor,
  invertColor,
  ...props
}) => {
  const [videoModalActive, setVideomodalactive] = useState(false);

  const openModal = (e) => {
    e.preventDefault();
    setVideomodalactive(true);
  };

  const closeModal = (e) => {
    e.preventDefault();
    setVideomodalactive(false);
  };

  const outerClasses = classNames(
    "hero section center-content",
    topOuterDivider && "has-top-divider",
    bottomOuterDivider && "has-bottom-divider",
    hasBgColor && "has-bg-color",
    invertColor && "invert-color",
    className
  );

  const innerClasses = classNames(
    "hero-inner section-inner",
    topDivider && "has-top-divider",
    bottomDivider && "has-bottom-divider"
  );

  return (
    <section {...props} className={outerClasses}>
      <div className="container-sm">
        <div className={innerClasses}>
          <div className="hero-content">
            <h1
              className="mt-0 mb-16 reveal-from-bottom"
              data-reveal-delay="200"
            >
              국내최고 핵판매샵{" "}
              <span className="text-color-primary">핵블리</span>
            </h1>
            <div className="container-xs">
              <p
                className="m-0 mb-32 reveal-from-bottom"
                data-reveal-delay="400"
              >
                서든 / 배그 / 옵치 / 롤 / 발로란트 / 랜덤계정 최저가 판매중
                <br />
                24시간 자동충전 및 원격서비스와 정지없는 제품들로
                <br />
                믿을 수 있는 업계1위
              </p>
              <div className="reveal-from-bottom" data-reveal-delay="600">
                <ButtonGroup>
                  <Button
                    tag="a"
                    color="primary"
                    wideMobile
                    href="https://ive.gg/"
                    target="_blank"
                  >
                    사이트 바로가기
                  </Button>
                  <Button
                    tag="a"
                    color="dark"
                    wideMobile
                    href="https://t.me/+gIE7go-ia45mY2Jh"
                    target="_blank"
                  >
                    텔레그램 입장하기
                  </Button>
                  <Button color="primary" wideMobile onClick={bookmark}>
                    북마크
                  </Button>
                </ButtonGroup>
              </div>
            </div>
          </div>
          <div
            className="hero-figure reveal-from-bottom illustration-element-01"
            data-reveal-value="20px"
            data-reveal-delay="800"
          >
            <a
              data-video="https://player.vimeo.com/video/783454485"
              href="#0"
              aria-controls="video-modal"
              onClick={openModal}
            >
              <Image
                className="has-shadow"
                src={
                  "https://cdn.discordapp.com/attachments/981826096775438396/1072143236128313365/2_22.png"
                }
                alt="Hero"
                width={896}
                height={504}
              />
            </a>
          </div>
          <Modal
            id="video-modal"
            show={videoModalActive}
            handleClose={closeModal}
            video="https://player.vimeo.com/video/783454485"
            videoTag="iframe"
          />
        </div>
      </div>
    </section>
  );
};

Hero.propTypes = propTypes;
Hero.defaultProps = defaultProps;

export default Hero;
